	// ==============================================================*/
	// Multimedia carousels
	// ==============================================================*/

		
	$('.carousel-section').each(function(){
		
		$this = $(this);

		// On before slide change
		$('.candy-carousel',$this).on('init', function(event, slick, currentSlide, nextSlide){
			
			$('.slick-center').prev().addClass('off-center');
			$('.slick-center').next().addClass('off-center');

		});

		$('.candy-carousel',$this).slick({
			slide: '.candy-slide',
			arrows: true,
			dots: false,
			slidesToShow: 5,
			slidesToScroll: 1,
			autoplay: true,
  			autoplaySpeed: 3000,
			focusOnSelect: false,
			prevArrow: '.previous-button',
			nextArrow: '.next-button',
			centerMode: true,
			centerPadding: '20px',
			responsive: [
			    {
			      breakpoint: 969,
			      settings: {
			        slidesToShow: 3,
					slidesToScroll: 1,
					centerPadding: '40px',
			      }
			    },
			    {
			      breakpoint: 680,
			      settings: {
			        slidesToShow: 1,
					slidesToScroll: 1,
					centerMode: true,
					centerPadding: '20px',
			      }
			    }
			    // You can unslick at a given breakpoint now by adding:
			    // settings: "unslick"
			    // instead of a settings object
			  ]
		});



		

		// On before slide change
		$('.candy-carousel',$this).on('beforeChange', function(event, slick, currentSlide, nextSlide){
			
			$('.candy-slide').removeClass('off-center');

		});

		// On after slide change
		$('.candy-carousel',$this).on('afterChange', function(event, slick, currentSlide, nextSlide){
			
			$('.slick-center').prev().addClass('off-center');
			$('.slick-center').next().addClass('off-center');

		});

	});

	$('.demo-form button').on('click', function(e){
		e.preventDefault();
	});

	// ==============================================================*/
	// Hero parallax feature
	// ==============================================================*/
	$window = $(window);

	$window.bind('scroll', function(e) {
		parallax();
	});

	// Subtle Parallax Function
	function parallax() {
		$scrollPosition = $window.scrollTop();
		$windowHeight = (( $window.height() ));
		$('.section-hero .section-background').css('background-position-y', (0 - ($scrollPosition * .15)) + 'px');
	};

